import Withdraw from "./withdraw-model.js";
const withdraw = new Withdraw()
export default {
	data() {
		return {
			userInfo: {},
			withdrawNum: '',
			imgAlipayShow: '',
			types: ''
		}
	},
	onLoad() {
		const that = this
		that._onLoad()
	},
	methods: {
		_onLoad(callBack) {
			// 使用vuex获取原有的用户信息
			const that = this
			that.userInfo = that.$store.state.userInfo;
		},
		// 上传图片
		getCard(callBack) {
			const that = this
			if (that.types == '') {
				withdraw.show_tips('请选择收款方式')
				return false
			}
			withdraw.getCard(1, (res) => {
				if (res.status_code == 'ok') {
					that.imgAlipayShow = res.data
					withdraw.show_tips(res.message)
				} else {
					withdraw.show_tips(res.message)
				}
			})
		},
		// 收款方式选择
		radioChange(e) {
			const that = this
			that.types = withdraw.get_input_val(e)
		},
		// 提现金额设置
		getWithdrawNum(e) {
			const that = this
			that.withdrawNum = withdraw.get_input_val(e)
		},
		// 提现
		runWithdraw() {
			const that = this
			if (that.withdrawNum == '') {
				withdraw.show_tips('请输入提现金额')
				return false
			}
			if(!/((^[1-9]\d*)|^0)(\.\d{0,2}){0,1}$/.test(that.withdrawNum)) {
				withdraw.show_tips('请输入合法金额')
				return false
			}
			if (that.imgAlipayShow == '') {
				withdraw.show_tips('请上传收款码')
				return false
			}
			if (that.types == '') {
				withdraw.show_tips('请选择收款方式')
				return false
			}
			withdraw.runWithdraw({
				money: that.withdrawNum,
				qrcode: that.imgAlipayShow,
				type: that.types
			}, (res) => {
				withdraw.show_tips(res.message)
				if (res.status_code == 'ok') {
					setTimeout(function(){
						withdraw.navigate_back()
					}, 2000)
				}
				
			})
		}
	},
	// 下拉刷新
	onPullDownRefresh() {
		var that = this;
		that.page = 1;
		that._onLoad(() => {
			uni.stopPullDownRefresh();
		});
	},
	//上拉加载更多
	// onReachBottom() {
	//   var that = this;
	//   if (that.last_page == that.page) {
	//     return;
	//   }
	//   that.page += 1;
	//   that.getListData();
	// },
	// 分享
	onShareAppMessage() {
		let shareData = {
			title: '',
			path: `pages/index/index?${this.userInfo.id}`,
			imageUrl: ''
		}
		return withdraw.onShareAppMessage(shareData);
	}
}
